import React, { Component } from 'react';
import Card from './components/Card/Card';
import Header from './components/Header/Header';
import Title from './components/Title/Title';
import Disclaimer from './components/Disclaimer/Disclaimer';
import { jsonResponse } from './components/copy';
import {Flickity} from './_Globals/js/flickity/flickity.pkgd.js';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props);

    const cardsList = jsonResponse.cards;
    console.log(cardsList.cardText);
    this.state = { cardsList };
  }
  componentDidMount(){
    document.title = "I am Jonas Van der Sande";
  }

  render() {
    return (
      <div className="App">
        <Header active="portfolio" />
        <Title copy="@ Wunderman / Thesedays" classes="title"/>
        <div className="card-container main-carousel" data-flickity='{ "cellAlign": "center",  "wrapAround": true }'>
            {this.state.cardsList.map((cardsList, index) => (
              <Card cardText={cardsList.cardText} cardTitle={cardsList.cardTitle} cardBackground={cardsList.cardBackground} cardContentType={cardsList.cardContentType} cardContentLink={cardsList.cardContentLink} cardContentLinkExt={cardsList.cardContentLinkExt} cardLegal={cardsList.cardLegal}/>
            ))}
        </div>
        <Disclaimer />
      </div>
    );
  }
}

export default App;



